ol,
ul {
  list-style: none;
}
body {
  background-color: #e2e2e2 !important  ;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif !important;
}

input,
button {
  outline: 0;
}

.login-signup {
  width: 25%;
  background-color: #e2e2e2;
  position: relative;
  margin: 0 auto;
  margin-top: 100px;
}

.login-signup-title {
  margin: 0;
  padding: 15px;
  text-align: center;
  font-size: 30px;
  color: #fff;
  background-color: #ff5722;
  border: 1px solid #f55320;
  font-weight: 400;
}

.login-signup-content {
  position: relative;
  padding: 25px;
  background-color: #fff;
  border: 1px solid #e2e2e2;
  /* margin-top: 25px; */
}

.input-name {
  font-size: 24px;
}

.input-name h2 {
  padding-bottom: 5px;
  float: left;
}

input.field-input {
  font-size: 24px;
  width: 100%;
  margin: 12px 0;
  background-color: rgba(2, 2, 2, 0.07);
  border: 1px solid rgba(0, 0, 0, 0.02);
  border-radius: 8px;
  padding: 6px 12px;
  border-radius: 20px;
  transition: 200ms cubic-bezier(0.72, 0.15, 0.53, 0.84);
}
input.field-input:hover,
input.field-input:focus {
  border: 1px solid #f55320;
}

/* .input-r {
  margin-top: 15px;
  position: relative;
} */

.submit-btn {
  width: 100%;
  margin-top: 28px;
  font-size: 24px;
  text-align: center;
  padding: 8px;
  border-radius: 20px;
  background-color: #ff5722;
  color: #fff;
  border: none;
  transition: 200ms cubic-bezier(0.72, 0.15, 0.53, 0.84);
  cursor: pointer;
}

.submit-btn:disabled {
  pointer-events: none;
  opacity: 0.2;
}
.submit-btn:hover {
  box-shadow: 1px 1px 5px #f4511e;
}

#signup .login-signup-content {
  padding-bottom: 46px;
}
